import { Col, Row, Spin, Modal } from 'antd';
import React, { useEffect, useState } from 'react';
import { LeftCircleFilled } from '@ant-design/icons';
import AlumniCard from '../../UiComponents/Others/AlumniCard';
import axios from 'axios';
const AlumniDetails = ({ match }) => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);
  const [messageModal, setmessageModal] = useState(false);
  const [alumniMessage, setalumniMessage] = useState();
  const {
    params: { alumniYear },
  } = match;
  useEffect(() => {
    setspin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/alumni`)
      .then((resp) => {
        setspin(false);
        console.log(resp.data);
        setdata(resp.data);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);

  return (
    <div className='alumni-details'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='page-top alumni-top'>
        <div className='pattern'></div>
        <header>
          <h2>About Our Alumni</h2>
          <p>Nobody is bothered about an institution more than its alumni</p>
        </header>
      </div>
      <div className='container'>
        <h4>
          <div className='back-btn'>
            <LeftCircleFilled className='icon' />
          </div>
          Alumni from the batch {alumniYear - 4} - {alumniYear}
        </h4>
        <div className='card-container '>
          <Row gutter={8}>
            {data &&
              data
                .filter((value) => value.batch == alumniYear)
                .map((value, index) => {
                  console.log(value);
                  return (
                    <Col md={8} key={index}>
                      <AlumniCard
                        imgSrc={value.profilePicture}
                        name={value.fullname}
                        headline={value.designation}
                        branch={value.branch}
                        about={value.about}
                        liLink={value.links[0].linkedin}
                        ghLink={value.links[0].github}
                        gmLink={value.links[0].email}
                        fbLink={value.links[0].facebook}
                        placed={value.placed}
                        message={value.message}
                        handleMessage={() => {
                          setalumniMessage(value.message);
                          setmessageModal(true);
                        }}
                      />
                    </Col>
                  );
                })}
          </Row>
        </div>
      </div>
      <Modal
        title='Alumni Message'
        visible={messageModal}
        onCancel={() => setmessageModal(false)}
        footer={null}
        className='message-modal'
      >
        <p>{alumniMessage}</p>
      </Modal>
    </div>
  );
};

export default AlumniDetails;
